$link-color: #00acdc;

body {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
  font-style: normal;
  line-height: 100%;
}

.header {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
}

.logo__image {
  width: 40px;
  height: 40px;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.nav__item {
  display: inline;
}

.nav__link {
  display: block;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  padding: 24px 0;
  margin-left: 20px;
  color: #060b35;
}

.nav__link:hover {
  color: $link-color;
}

.nav__link.is-active {
  color: $link-color;
  position: relative;
}

.nav__link::after {
  content: "";
  width: 100%;
  height: 4px;
  border-radius: 8px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link.is-active::after {
  background: $link-color;
}

.nav__list:nth-child(1) .nav__link.is-active {
  margin-left: 0;
}
